
"use client";

import { useState, type FormEvent, useEffect } from "react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { useAuth } from "@/contexts/auth-context";
import { APP_NAME } from "@/lib/constants";
import { Loader2, KeyRound } from "lucide-react";

export default function LoginPage() {
  const [identifier, setIdentifier] = useState(""); // Email or Phone
  const [otp, setOtp] = useState("");
  const [otpSent, setOtpSent] = useState(false);
  const { login, isLoading, isAuthenticated } = useAuth();
  const router = useRouter();

  useEffect(() => {
    if (isAuthenticated) {
      router.push("/dashboard");
    }
  }, [isAuthenticated, router]);


  const handleSendOtp = async (e: FormEvent) => {
    e.preventDefault();
    // Simulate sending OTP
    console.log("Sending OTP to:", identifier);
    await new Promise(resolve => setTimeout(resolve, 1000)); // Simulate API call
    setOtpSent(true);
    // In a real app, you'd call an API to send OTP
  };

  const handleLogin = async (e: FormEvent) => {
    e.preventDefault();
    // Basic validation
    if (!identifier || (otpSent && !otp)) {
      alert("Please fill in all fields.");
      return;
    }
    // Simulate login with dummy user data
    await login({ id: "1", name: identifier.split('@')[0] || "Demo User", email: identifier }, otp);
  };

  if (isAuthenticated) {
    return null; // Or a loading spinner while redirecting
  }

  return (
    <div className="flex min-h-screen items-center justify-center bg-gradient-to-br from-primary/10 via-background to-background p-4">
      <Card className="w-full max-w-md shadow-2xl">
        <CardHeader className="text-center">
          <div className="flex justify-center mb-4">
            <KeyRound className="h-12 w-12 text-primary" />
          </div>
          <CardTitle className="text-3xl font-bold">{APP_NAME} Login</CardTitle>
          <CardDescription>
            {otpSent ? "Enter the OTP sent to your device." : "Sign in to access your dashboard."}
          </CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={otpSent ? handleLogin : handleSendOtp} className="space-y-6">
            {!otpSent ? (
              <div className="space-y-2">
                <Label htmlFor="identifier">Email or Phone</Label>
                <Input
                  id="identifier"
                  type="text"
                  placeholder="you@example.com or +1234567890"
                  value={identifier}
                  onChange={(e) => setIdentifier(e.target.value)}
                  required
                  disabled={isLoading}
                />
              </div>
            ) : (
              <div className="space-y-2">
                <Label htmlFor="otp">One-Time Password</Label>
                <Input
                  id="otp"
                  type="text"
                  placeholder="Enter your OTP"
                  value={otp}
                  onChange={(e) => setOtp(e.target.value)}
                  required
                  disabled={isLoading}
                  maxLength={6}
                />
              </div>
            )}
            <Button type="submit" className="w-full" disabled={isLoading}>
              {isLoading ? (
                <Loader2 className="mr-2 h-4 w-4 animate-spin" />
              ) : otpSent ? (
                "Verify OTP & Login"
              ) : (
                "Send OTP"
              )}
            </Button>
          </form>
        </CardContent>
        <CardFooter className="flex flex-col items-center text-sm">
           {otpSent && (
             <Button variant="link" size="sm" onClick={() => { setOtpSent(false); setOtp('');}} disabled={isLoading}>
                Change Email/Phone
             </Button>
           )}
          <p className="mt-4 text-muted-foreground">
            Don&apos;t have an account?{" "}
            <Link href="/#" className="font-medium text-primary hover:underline">
              Sign up (Not implemented)
            </Link>
          </p>
        </CardFooter>
      </Card>
    </div>
  );
}
